import React, {useState} from 'react';
import s from '../stylesForClock/alertSlide.module.css';
import Button from "@mui/material/Button";


interface AlertSlideProps {
    label: string;
    isPlaying: boolean;
    onTogglePlay: () => void;
    onFormClose:()=>void;
}


const AlertSlide: React.FC<AlertSlideProps> = ({ label, isPlaying, onTogglePlay, onFormClose }) => {

    return (
        <div className={s.overlay} onClick={onFormClose}>
            <div className={s.alertSlide} onClick={(e) => e.stopPropagation()}>
                <h1> Alarm </h1>
                <span>is ringing!</span>
                <p>{label}</p>
                <Button classes={{root: s.customTextButton}} onClick={() => {
                    onTogglePlay();
                    onFormClose();
                }}>
                    {isPlaying ? 'Stop' : "Close"}
                </Button>
            </div>
        </div>
    );
};

export default AlertSlide;
