import React, {useEffect, useState} from "react";
import s from "../stylesForClock/timer.module.css";
import {IconButton, Select} from "@mui/material";
import {useNavigate} from "react-router-dom";
import {SelectChangeEvent} from "@mui/material/Select";
import FormControl from "@mui/material/FormControl";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import {ArrowBack} from "@mui/icons-material";
import SettingsIcon from "@mui/icons-material/Settings";


const Timer = () => {
    const [hours, setHours] = useState(0);
    const [minutes, setMinutes] = useState(0);
    const [seconds, setSeconds] = useState(0);
    const [inputHours, setInputHours] = useState(0);
    const [inputMinutes, setInputMinutes] = useState(0);
    const [inputSeconds, setInputSeconds] = useState(0);
    const [isOn, setIsOn] = useState(false);
    const [open, setOpen] = useState(false);

    const onReset = () => {
        setIsOn(false);
        setInputHours(0);
        setInputMinutes(0);
        setInputSeconds(0);
        setHours(0);
        setMinutes(0);
        setSeconds(0);
    };

    useEffect(() => {
        let interval: NodeJS.Timer;
        if (isOn) {
            interval = setInterval(() => {
                setSeconds((prevSeconds) => {
                    if (prevSeconds > 0) {
                        return prevSeconds - 1;
                    } else if (minutes > 0) {
                        setMinutes((prevMinutes) => prevMinutes - 1);
                        return 59;
                    } else if (hours > 0) {
                        setHours((prevHours) => prevHours - 1);
                        setMinutes(59);
                        return 59;
                    } else {
                        clearInterval(interval);
                        setIsOn(false);
                        return 0;
                    }
                });
            }, 1000);
        }
        return () => clearInterval(interval);
    }, [isOn, minutes, hours]);

    const navigate = useNavigate();

    const handleClose = () => {
        setOpen(false);
    };

    const handleOpen = () => {
        setOpen(true);
    };

    const hoursArray = Array.from({length: 24}, (_, i) => i);
    const minutesArray = Array.from({length: 60}, (_, i) => i);
    const secondsArray = Array.from({length: 60}, (_, i) => i);

    const selectHours = (e: SelectChangeEvent<number>) => {
        const value = Number(e.target.value);
        setInputHours(value);
    };

    const selectMinutes = (e: SelectChangeEvent<number>) => {
        const value = Number(e.target.value);
        setInputMinutes(value);
    };

    const selectSeconds = (e: SelectChangeEvent<number>) => {
        const value = Number(e.target.value);
        setInputSeconds(value);
    };

    const startTimer = () => {
        if (!isOn) {
            if (hours === 0 && minutes === 0 && seconds === 0) {
                setHours(inputHours);
                setMinutes(inputMinutes);
                setSeconds(inputSeconds);
            }
            setIsOn(true);
        }
    };

    return (
        <div className={s.timerBody}>
            <div className={s.timerWrapper}>
                <span className={s.timerTitle}>Timer</span>
                <div className={s.mainTimerCircle}>
                    <div className={s.secondTimerCircle}>
                        <div className={s.timerInner}>
                            <div className={s.timerSegment}>
                                <span className={s.time}>{hours}</span>

                            </div>
                            <div className={s.timerSegment}>
                                <span className={s.time}>{minutes}</span>

                            </div>
                            <div className={s.timerSegment}>
                                <span className={s.time}>{seconds}</span>

                            </div>
                        </div>
                    </div>
                </div>

                <div className={s.inputs}>
                    <FormControl className={s.formControl}>
                        <InputLabel id="hours-select-label">Hours</InputLabel>
                        <Select
                            labelId="hours-select-label"
                            id="hours-select"
                            onClose={handleClose}
                            onOpen={handleOpen}
                            value={inputHours}
                            label="Hours"
                            onChange={selectHours}
                        >
                            {hoursArray.map((hour) => (
                                <MenuItem key={hour} value={hour}>
                                    {hour}
                                </MenuItem>
                            ))}
                        </Select>
                    </FormControl>

                    <FormControl className={s.formControl}>
                        <InputLabel id="minutes-select-label">Minutes</InputLabel>
                        <Select
                            labelId="minutes-select-label"
                            id="minutes-select"
                            onClose={handleClose}
                            onOpen={handleOpen}
                            value={inputMinutes}
                            label="Minutes"
                            onChange={selectMinutes}
                        >
                            {minutesArray.map((min) => (
                                <MenuItem key={min} value={min}>
                                    {min}
                                </MenuItem>
                            ))}
                        </Select>
                    </FormControl>

                    <FormControl className={s.formControl}>
                        <InputLabel id="seconds-select-label">Seconds</InputLabel>
                        <Select
                            labelId="seconds-select-label"
                            id="seconds-select"
                            onClose={handleClose}
                            onOpen={handleOpen}
                            value={inputSeconds}
                            label="Seconds"
                            onChange={selectSeconds}
                        >
                            {secondsArray.map((sec) => (
                                <MenuItem key={sec} value={sec}>
                                    {sec}
                                </MenuItem>
                            ))}
                        </Select>
                    </FormControl>

                    <div className={s.buttons}>
                        <button
                            className={s.resetTimerButton}
                            onClick={onReset}
                            disabled={!isOn && seconds === 0}
                        >
                            Reset
                        </button>
                        {!isOn && (
                            <button
                                className={s.startTimerButton}
                                onClick={startTimer}
                                disabled={isOn || (inputHours === 0 && inputMinutes === 0 && inputSeconds === 0)}
                            >
                                Start
                            </button>
                        )}
                        {isOn && (
                            <button className={s.stopTimerButton} onClick={() => setIsOn(false)}>
                                Stop
                            </button>
                        )}
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Timer;
