import React, {useEffect, useRef, useState} from "react";
import s from '../stylesForClock/alarm.module.css';
import AnalogClock from "./AnalogClock";
import IOSSwitch from "./iosSwitch";
import AddCircleIcon from '@mui/icons-material/AddCircle';
import AlertDialogSlide from "./dialogForm";
import SoundPlayer from "./playFunction";
import AlertSlide from "./alarmAlertSlide";

interface Day {
    id: number;
    name: string;
}

interface Melody {
    name: string;
    url: string;
}

interface Alarm {
    hours: number;
    minutes: number;
    active: boolean;
    snoozed: boolean;
    snoozeEndTime?: Date; //        
    label: string;
    days: Day[];
    melodies: Melody[];
}

const Alarm = () => {
    const [currentTime, setCurrentTime] = useState(new Date());
    const [isOpen, setIsOpen] = useState(false);
    const [isPlaying, setIsPlaying] = useState<'PLAYING' | 'STOPPED'>('STOPPED');
    const [alarms, setAlarms] = useState<Alarm[]>([
        {
            hours: 18, minutes: 8, active: false, snoozed: false, label: "test",
            days: [{name: "Monday", id: 0}],
            melodies: [{
                name: "Sun",
                url: process.env.PUBLIC_URL + "/chiptune-alarm-clock-112869"
            }],
        }
    ]);
    const [alarmTriggered, setAlarmTriggered] = useState<Alarm | null>(null);
    const [currentMelody, setCurrentMelody] = useState<{ url: string } | null>(null);
    const delayTimeoutRef = useRef<NodeJS.Timeout | null>(null);

    const openForm = () => setIsOpen(true);
    const handleCloseForm = () => setIsOpen(false);

    const addAlarm = (
        hours: number,
        minutes: number,
        active: boolean,
        days: Day[],
        melodies: Melody | undefined,
        label: string
    ) => {
        setAlarms([...alarms, {
            hours,
            label,
            minutes,
            active,
            snoozed: false,
            days,
            melodies: melodies ? [melodies] : []
        }]);
        handleCloseForm();
    };

    const toggleAlarmActive = (index: number) => {
        if (delayTimeoutRef.current) {
            clearTimeout(delayTimeoutRef.current);
            delayTimeoutRef.current = null;
        }
        setAlarms((prevAlarms) =>
            prevAlarms.map((alarm, i) =>
                i === index ? {...alarm, active: !alarm.active, snoozed: false, snoozeEndTime: undefined} : alarm
            )
        );
    };

    const checkAlarms = () => {
        const now = new Date();
        const currentHours = now.getHours();
        const currentMinutes = now.getMinutes();

        alarms.forEach((alarm) => {
            if (alarm.active) {
                if (alarm.snoozed && alarm.snoozeEndTime && now >= alarm.snoozeEndTime) {
                    //      ,  snoozed   
                    setAlarmTriggered(alarm);
                    setCurrentMelody({url: `${process.env.PUBLIC_URL}${alarm.melodies[0].url}`});
                    setIsPlaying('PLAYING');
                    setAlarms(prevAlarms =>
                        prevAlarms.map(a => (a === alarm ? {...a, snoozed: false, snoozeEndTime: undefined} : a))
                    );
                } else if (!alarm.snoozed && alarm.hours === currentHours && alarm.minutes === currentMinutes) {
                    //   
                    setAlarmTriggered(alarm);
                    setCurrentMelody({url: `${process.env.PUBLIC_URL}${alarm.melodies[0].url}`});
                    setIsPlaying('PLAYING');
                }
            }
        });

        if (!alarms.some(alarm => alarm.active)) {
            setIsPlaying('STOPPED');
            setCurrentMelody(null);
        }
    };
    const handleCloseAlertSlide = () => {
        setAlarmTriggered(null);
    };
    const handleStopAlarm = () => {
        setIsPlaying("STOPPED");
        if (alarmTriggered) {
            snoozeAlarm(alarmTriggered); //     ""
        }
        handleCloseAlertSlide();
    };


    const snoozeAlarm = (alarm: Alarm) => {
        const snoozeDuration = 5 * 60 * 1000; // 5 
        const snoozeEndTime = new Date(Date.now() + snoozeDuration);

        setAlarms((prevAlarms) =>
            prevAlarms.map((a) =>
                a === alarm ? {...a, snoozed: true, snoozeEndTime} : a
            )
        );
    };

    useEffect(() => {
        const interval = setInterval(() => {
            setCurrentTime(new Date());
            checkAlarms();
        }, 1000);
        return () => clearInterval(interval);
    }, [alarms]);

    useEffect(() => {
        return () => {
            if (delayTimeoutRef.current) {
                clearTimeout(delayTimeoutRef.current);
            }
        };
    }, []);

    const handleDaysOutput = (selectedDays: Day[]) => {
        if (selectedDays.length === 7) return "Everyday";

        const weekdays = selectedDays.filter(day => day.id >= 1 && day.id <= 5); // Monday to Friday
        const weekends = selectedDays.filter(day => day.id === 6 || day.id === 7); // Saturday and Sunday

        //               ,    
        if (weekdays.length > 0 && weekends.length > 0) {
            return selectedDays.map(day => day.name).join(", ");
        }

        //     ,    
        if (weekdays.length === 5 && weekends.length === 0) return "Weekdays";

        //     
        if (weekends.length === 2 && weekdays.length === 0) return "Weekends";

        //    ,     
        return selectedDays.map(day => day.name).join(", ");
    };

    return (
        <div className={s.alarmBody}>
            <span className={s.alarmTitle}>Alarm</span>
            <AnalogClock date={currentTime}/>
            <div className={s.alarmsList}>
                {alarms.map((alarm, index) => (
                    <div className={s.listElement} key={index}>
                        <div className={s.cardContent}>
                            <div className={s.timeCard}>
                                <span className={alarm.active ? s.activeText : s.dimmedText}>
                                    {`${alarm.hours}:${alarm.minutes < 10 ? `0${alarm.minutes}` : alarm.minutes}`}
                                </span>
                                <span className={alarm.active ? s.activeText : s.dimmedText}>
                                    {`${alarm.days.length > 0 ? handleDaysOutput(alarm.days) : "Today"}`}
                                </span>
                            </div>
                            <IOSSwitch checked={alarm.active} onChange={() => toggleAlarmActive(index)}/>
                        </div>
                    </div>
                ))}
                <div className={s.addAlarmButtonBlock}>
                    <AddCircleIcon classes={{root: s.customAddAlarmButton}} onClick={openForm}/>
                </div>
            </div>

            {isOpen && <AlertDialogSlide handleClose={handleCloseForm} addAlarm={addAlarm}/>}
            {alarmTriggered && (
                <AlertSlide
                    label={alarmTriggered.label}
                    isPlaying={isPlaying === 'PLAYING'}
                    onTogglePlay={handleStopAlarm}
                    onFormClose={handleCloseAlertSlide}
                />
            )}
            <SoundPlayer
                playStatus={isPlaying}
                loop={true}
                url={currentMelody?.url || ''}
            />
        </div>
    );
};

export default Alarm;
