import React from 'react';
import Sound from 'react-sound';

interface SoundPlayerProps {
    playStatus: 'PLAYING' | 'STOPPED';
    loop?: boolean;
    url: string | null;
}

const SoundPlayer: React.FC<SoundPlayerProps> = ({playStatus, loop = false, url}) => {
    return (<Sound url={url} playStatus={playStatus === 'PLAYING' ? Sound.status.PLAYING : Sound.status.STOPPED}
                   loop={loop}/>);
};
export default SoundPlayer;