import React, {useState, useEffect} from 'react';
import s from '../stylesForClock/s.module.css';
import {IconButton} from "@mui/material";
import AnalogClock from "./AnalogClock";
import {ObjectDate} from "./date";
import SettingsIcon from '@mui/icons-material/Settings';
const Clock = () => {
    const [time, setTime] = useState({hour: '', minute: '', second: ''});

    useEffect(() => {
        const interval = setInterval(() => {
            const dateObject = new Date();
            const hour = dateObject.getHours().toString().padStart(2, '0');
            const minute = dateObject.getMinutes().toString().padStart(2, '0');
            const second = dateObject.getSeconds().toString().padStart(2, '0');

            setTime({hour, minute, second});

        }, 1000);

        return () => clearInterval(interval);
    }, []);


    return (
        <div className={s.body}>
           {/* <div className={s.linkButtons}>

                <div className={s.settingArrow}>
                    <IconButton color="inherit">
                        <SettingsIcon fontSize={"large"} color={"inherit"}/>
                    </IconButton>
                </div>
            </div>*/}
            <span className={s.clockTitle}>Clock</span>
            <div className={s.ContentContainer}>
                <div className={s.clockContainer}>

                    <AnalogClock date={new Date()}/>

                    <div className={s.DefaultClockAndDateContainer}>
                        <div className={s.DefaultClockBlock}>
                            <div className={s.timeBox}>{time.hour}</div>
                            <div className={s.timeBox}>{time.minute}</div>
                            <div className={s.timeBox}>{time.second}</div>
                        </div>

                    <div className={s.dateTimeContainer}>
                        <ObjectDate/>
                    </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Clock;
