import * as React from 'react';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import Checkbox from '@mui/material/Checkbox';
import {useState} from "react";
import s from '../stylesForClock/melodisList.module.css';
import Button from "@mui/material/Button";

interface CheckBoxMelodyListProps {
    onMelodyChange: (selectedMelody: { id: number, name: string, url: string }) => void; //    id,  name
    toggleMelodyList: () => void;
}

export default function CheckboxMelodyList({onMelodyChange, toggleMelodyList}: CheckBoxMelodyListProps) {
    const [checked, setChecked] = React.useState<number[]>([]);
    const [melodies] = useState([
        {id: 1, name: "Sunrise", url: "/melodys/japen-eas-alarm-1945-249123.mp3"},
        {id: 2, name: "Sundown", url: "/melodys/chiptune-alarm-clock-112869.mp3"},
        {id: 3, name: "BlueSky", url: "/melodys/estonia-eas-alarm-1984-249124.mp3"},
        {
            id: 4,
            name: "oldTree",
            url: "/melodys/alarmclock-bell-ringing-clear-windingdown-000212_0029s3_d-095-099-031-042-35592.mp3"
        },
    ]);

    const handleToggle = (value: number) => () => {
        const currentIndex = checked.indexOf(value);
        const newChecked = [...checked];

        if (currentIndex === -1) {
            newChecked.push(value);
        } else {
            newChecked.splice(currentIndex, 1);
        }

        setChecked(newChecked);

        const selectedMelody = melodies.find((melody) => melody.id === value);
        if (selectedMelody) {
            onMelodyChange(selectedMelody);
        }
        //     ID  

    };

    const handleToggleMelodyList = () => {
        toggleMelodyList();
    };

    return (
        <div className={s.dayList}>
            {melodies.map((melody) => {
                const labelId = `checkbox-list-label-${melody.id}`;
                return (
                    <div className={s.items} key={melody.id}>
                        <ListItemButton role={undefined} onClick={handleToggle(melody.id)} dense>
                            <ListItemIcon>
                                <Checkbox
                                    edge="start"
                                    checked={checked.includes(melody.id)}
                                    tabIndex={-1}
                                    disableRipple
                                    inputProps={{'aria-labelledby': labelId}}
                                />
                            </ListItemIcon>
                            <ListItemText id={labelId} primary={melody.name}/>
                        </ListItemButton>
                    </div>
                );
            })}
                <Button classes={{root: s.customTextButton}} onClick={handleToggleMelodyList}>Back</Button>
        </div>
    );
}
