import * as React from 'react';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import Checkbox from '@mui/material/Checkbox';
import {useState} from "react";
import s from '../stylesForClock/dayList.module.css';
import Button from "@mui/material/Button";

interface CheckBoxListProps {
    onDaysChange: (selectedDays: { id: number, name: string }[]) => void; //    id,  name
    toggleDaysList: () => void;

}

export default function CheckboxList({ onDaysChange, toggleDaysList }: CheckBoxListProps) {
    const [checked, setChecked] = React.useState<number[]>([]);
    const [days] = useState([
        { id: 1, name: "Monday", shortName: "Mon" },
        { id: 2, name: "Tuesday", shortName: "Tue" },
        { id: 3, name: "Wednesday", shortName: "Wed" },
        { id: 4, name: "Thursday", shortName: "Thu" },
        { id: 5, name: "Friday", shortName: "Fri" },
        { id: 6, name: "Saturday", shortName: "Sat" },
        { id: 7, name: "Sunday", shortName: "Sun" },
    ]);

    const handleToggle = (value: number) => () => {
        const currentIndex = checked.indexOf(value);
        const newChecked = [...checked];

        if (currentIndex === -1) {
            newChecked.push(value);
        } else {
            newChecked.splice(currentIndex, 1);
        }

        setChecked(newChecked);

        //     ID  
        const selectedDays = days
            .filter((day) => newChecked.includes(day.id))
            .map((day) => ({
                id: day.id,
                name: day.name,
                shortName: day.shortName
            }));

        //       
        if (selectedDays.length > 1) {
            const shortsDays = selectedDays.map((day) => day.shortName); //   shortName   
            onDaysChange(shortsDays.map((shortName, index) => ({
                id: selectedDays[index].id,
                name: shortName //  shortName    
            })));
        } else {
            onDaysChange(selectedDays); //   ,    
        }
    };

    const handleToggleDayList = () => {
        toggleDaysList();
    };

    return (
        <div className={s.dayList}>
            {days.map((day) => {
                const labelId = `checkbox-list-label-${day.id}`;
                return (
                    <div className={s.items} key={day.id}>
                        <ListItemButton role={undefined} onClick={handleToggle(day.id)} dense>
                            <ListItemIcon>
                                <Checkbox
                                    edge="start"
                                    checked={checked.includes(day.id)}
                                    tabIndex={-1}
                                    disableRipple
                                    inputProps={{ 'aria-labelledby': labelId }}
                                />
                            </ListItemIcon>
                            <ListItemText id={labelId} primary={day.name} />
                        </ListItemButton>
                    </div>
                );
            })}
            <Button classes={{ root: s.customTextButton }} onClick={handleToggleDayList}>Back</Button>
        </div>
    );
}
