import { NavLink } from "react-router-dom";
import clock from "../icons/clock.svg";
import clockWhite from "../icons/clock1.svg";
import stopwatch from "../icons/stopwatch.svg";
import timer from "../icons/timer.svg";
import timerWhite from "../icons/timer1.svg";
import stopWatchWhite from "../icons/stopwatch1.svg";
import alarm from "../icons/alarm.svg";
import alarmWhite from "../icons/alarm1.svg";
import s from "../stylesForClock/navBar.module.css";

const Navbar = () => {
    return (
        <div className={s.container}>
            <nav className={s.nav}>
                {/*  */}
                <NavLink to="/clock" className={({ isActive }) => `${s.item} ${isActive ? s.activeItem : ''}`}>
                    {({ isActive }) => <img alt="clock" src={isActive ? clockWhite : clock} />}
                </NavLink>

                {/*  */}
                <NavLink to="/stopwatch" className={({ isActive }) => `${s.item} ${isActive ? s.activeItem : ''}`}>
                    {({ isActive }) => <img alt="stopwatch" src={isActive ? stopWatchWhite : stopwatch} />}
                </NavLink>

                {/*  */}
                <NavLink to="/timer" className={({ isActive }) => `${s.item} ${isActive ? s.activeItem : ''}`}>
                    {({ isActive }) => <img alt="timer" src={isActive ? timerWhite : timer} />}
                </NavLink>

                {/*  */}
                <NavLink to="/alarm" className={({ isActive }) => `${s.item} ${isActive ? s.activeItem : ''}`}>
                    {({ isActive }) => <img alt="alarm" src={isActive ? alarmWhite : alarm} />}
                </NavLink>
            </nav>
        </div>
    );
};

export default Navbar;
