import React, {useEffect, useState} from "react";
import ss from "../stylesForClock/ss.module.css";
import {useNavigate} from "react-router-dom";
import {ArrowBack} from "@mui/icons-material";
import {IconButton} from "@mui/material";
import SettingsIcon from '@mui/icons-material/Settings';


const Stopwatch = () => {
        const [milliseconds, setMilliseconds] = useState(0)
        const [seconds, setSeconds] = useState(0);
        const [minutes, setMinutes] = useState(0);
        const [hours, setHours] = useState(0);
        const [isOn, setIsOn] = useState(false)
        const [laps, setLaps] = useState<String[]>([])

        const onReset = () => {
            setIsOn(false);
            setMilliseconds(0)
            setSeconds(0);
            setMinutes(0);
            setHours(0);
            setLaps([]);
        };


        useEffect(() => {
            const interval = setInterval(() => {
                if (isOn) {
                    setMilliseconds((prevMilliseconds) => {
                        if (prevMilliseconds === 999) {
                            setSeconds((prevSeconds) => {
                                if (prevSeconds === 59) {
                                    setMinutes((prevMinutes) => {
                                        if (prevMinutes === 59) {
                                            setHours((prevHours) => prevHours + 1);
                                            return 0;
                                        }
                                        return prevMinutes + 1;
                                    });
                                    return 0;
                                }
                                return prevSeconds + 1;
                            });
                            return 0;
                        }
                        return prevMilliseconds + 1;
                    });
                }
            }, 1);
            return () => clearInterval(interval);
        }, [isOn]);


        const lap = () => {
            setLaps((prevLaps) => [
                ...prevLaps,
                `${hours}:${minutes}:${seconds}:${milliseconds.toString().padStart(3, "0")}`
            ]);
        }

        return (
            <div className={ss.body}>
                <div className={ss.linkButtons}>

                    <div className={ss.settingArrow}>
                      {/*  <IconButton color="inherit">
                            <SettingsIcon fontSize={"large"} color={"inherit"}/>
                        </IconButton>*/}
                    </div>
                </div>
                <span className={ss.stopWatchTitle}>Stopwatch</span>
                <div className={ss.clockContainer}>
                    <div className={ss.numbersContainer}>
                        <div className={ss.numbersBlock}>

                            {hours > 0 && (
                                <>
                                    <div className={ss.clockElements}>{hours}</div>
                                    <div className={ss.clockSymbols}>:</div>
                                </>
                            )}

                            {minutes > 0 && (
                                <>
                                    <div className={ss.clockElements}>{minutes}</div>
                                    <div className={ss.clockSymbols}>:</div>
                                </>
                            )}
                            <div className={ss.clockElements}>{seconds}</div>
                            <div className={ss.clockSymbol}>,</div>
                            <div className={ss.clockElements}>{milliseconds.toString().padStart(2, "0")}</div>
                        </div>

                    </div>
                </div>
                <div className={ss.buttons}>
                    {isOn && (

                        <button className={ss.lapButton} onClick={lap} disabled={!isOn}>
                            Lap
                        </button>
                    )
                    }
                    {!isOn && (
                        <button className={ss.resetButton} onClick={onReset} disabled={milliseconds === 0}>
                            Reset
                        </button>
                    )

                    }

                    {
                        !isOn && (
                            <button className={ss.startButton} onClick={() => setIsOn(true)}>
                                Start
                            </button>
                        )
                    }

                    {
                        isOn && (
                            <button className={ss.stopButton} onClick={() => setIsOn(false)}>
                                Stop
                            </button>
                        )
                    }

                </div>
                <div className={ss.lapsContainer}>
                    {laps.length > 0 && <span className={ss.lapsH}>Lap Times: </span>}
                    <ul>
                        {laps.map((lap, index) => (
                            <>
                                <li key={index} className={ss.lapItem}>
                                    {`Lap ${index + 1}: ${lap}`}

                                </li>
                                <div className={ss.lapLine}></div>
                            </>
                        ))}
                    </ul>
                </div>
            </div>


        )
            ;
    }
;

export default Stopwatch;

