import React from 'react';
import '../stylesForClock/analogClock.css';

export type AnalogClockPropsType = {
    date: Date;
};

const AnalogClock: React.FC<AnalogClockPropsType> = ({ date }) => {
    const secondsStyle = {
        transform: `rotate(${date.getSeconds() * 6}deg)`
    };
    const minutesStyle = {
        transform: `rotate(${date.getMinutes() * 6}deg)`
    };
    const hoursStyle = {
        transform: `rotate(${(date.getHours() % 12) * 30}deg)`
    };

    return (
        <div className="preClock">

            <div className="clock">

                <div className="analog-clock">
                    <div className="dial seconds" style={secondsStyle}/>
                    <div className="dial minutes" style={minutesStyle}/>
                    <div className="dial hours" style={hoursStyle}/>

                    <div className="tick-mark0deg" style={{transform: 'rotate(0deg)'}}/>
                    <div className="tick-mark90deg" style={{transform: 'rotate(90deg)'}}/>
                    <div className="tick-mark180deg" style={{transform: 'rotate(180deg)'}}/>
                    <div className="tick-mark270deg" style={{transform: 'rotate(270deg)'}}/>
                </div>
            </div>
        </div>
    );
};

export default AnalogClock;
