import * as React from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import Slide from '@mui/material/Slide';
import {TransitionProps} from '@mui/material/transitions';
import s from '../stylesForClock/dialogForm.module.css';
import FormControl from "@mui/material/FormControl";
import InputLabel from "@mui/material/InputLabel";
import {Select} from "@mui/material";
import MenuItem from "@mui/material/MenuItem";
import {useState} from "react";
import {SelectChangeEvent} from "@mui/material/Select";
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
import CheckboxList from "./variantsOfDays";
import CheckboxMelodyList from "./variantsOfMelody";

//    
const Transition = React.forwardRef(function Transition(
    props: TransitionProps & {
        children: React.ReactElement<any, any>;
    },
    ref: React.Ref<unknown>,
) {
    return <Slide direction="up" ref={ref} {...props} />;
});

interface AlertDialogSlideProps {
    handleClose: () => void;
    addAlarm: (hours: number, minutes: number, active: boolean, days: { id: number; name: string }[], melodies: {
        id: number,
        name: string,
        url: string
    } | undefined,label:string) => void;
}

//   handleClose
export default function AlertDialogSlide({handleClose, addAlarm}: AlertDialogSlideProps) {
    const [open, setOpen] = React.useState(true);
    const [isOpen, setIsOpen] = React.useState(true);
    const [inputHours, setInputHours] = useState(0);
    const [inputMinutes, setInputMinutes] = useState(0);
    const [isActive, setIsActive] = useState(false)
    const [openDaysList, setOpenDaysList] = useState(false);
    const [selectedDays, setSelectedDays] = useState<{ id: number; name: string }[]>([]);
    const [openMelodyList, setOpenMelodyList] = useState(false);
    const [selectedMelody, setSelectedMelody] = useState<{ id: number; name: string; url: string } | undefined>();
    const[isEditing,setIsEditing]=useState(false);
    const[label,setLabel]=useState("Alarm");

    const handleLabelClick=()=>setIsEditing(true);
    const handleBlur=()=>setIsEditing(false);
    const handleChange=(e:React.ChangeEvent<HTMLInputElement>)=>{
        setLabel(e.target.value)
    }
    const handleCloseDaysList = () => {
        setOpenDaysList(false);
    };

    const handleCloseMelodyList = () => {
        setOpenMelodyList(false);
    };

    const handleOpenMelodyList = () => {
        setOpenMelodyList(true)
    }

    //   (  handleClose)
    const closeDialog = () => {
        setOpen(false);
        handleClose();
    };

    const handleCloseInput = () => {
        setIsOpen(false);
    };
    const handleOpenDaysList = () => {
        setOpenDaysList(true);
    }
    const handleOpenInput = () => {
        setIsOpen(true);
    };
    const hoursArray = Array.from({length: 24}, (_, i) => i);
    const minutesArray = Array.from({length: 60}, (_, i) => i);
    const selectHours = (e: SelectChangeEvent<number>) => {
        const value = Number(e.target.value);
        setInputHours(value);
    };
    const selectMinutes = (e: SelectChangeEvent<number>) => {
        const value = Number(e.target.value);
        setInputMinutes(value);
    };
    const handleAddAlarm = () => {
        addAlarm(inputHours, inputMinutes, isActive, selectedDays, selectedMelody,label)
    }

    const handleDaysOutputForForm = (selectedDays: { id: number, name: string }[]) => {
        if (selectedDays.length === 7) return "Everyday";

        const weekdays = selectedDays.filter(day => day.id >= 1 && day.id <= 5); // Monday to Friday
        const weekends = selectedDays.filter(day => day.id === 6 || day.id === 7); // Saturday and Sunday

        //               ,    
        if (weekdays.length > 0 && weekends.length > 0) {
            return selectedDays.map(day => day.name).join(", ");
        }

        //     ,    
        if (weekdays.length === 5 && weekends.length === 0) return "Weekdays";

        //     
        if (weekends.length === 2 && weekdays.length === 0) return "Weekends";

        //    ,     
        return selectedDays.map(day => day.name).join(", ");
    };
    const handleMelodyOutPutForForm = (selectedMelody: { id: number, name: string, url: string } | undefined) => {
        return selectedMelody?.name
    }

//   
    const handleDaysChange = (days: { id: number, name: string }[]) => {
        setSelectedDays(days);
    };

    const handleMelodyChange = (melody: { id: number, name: string, url: string } | undefined) => {
        if (melody) {
            setSelectedMelody(melody);
        }
    }
    return (
        <React.Fragment>
            <Dialog
                open={open}
                TransitionComponent={Transition}
                keepMounted
                onClose={closeDialog}
                aria-describedby="alert-dialog-slide-description"
            >
                <div className={openDaysList ? s.dialogContainerLarge : s.dialogContainerSmall}>
                    <div className={s.dialogHeader}>
                        <span className={s.dialogTitle}>
                        Add Alarm
                    </span>
                    </div>
                    <div className={s.inputs}>
                        <FormControl className={s.formControl}>
                            <InputLabel id="hours-select-label">Hours</InputLabel>
                            <Select
                                labelId="hours-select-label"
                                id="hours-select"
                                onClose={handleCloseInput}
                                onOpen={handleOpenInput}
                                value={inputHours}
                                label="Hours"
                                onChange={selectHours}
                            >
                                {hoursArray.map((hour) => (
                                    <MenuItem key={hour} value={hour}>
                                        {hour}
                                    </MenuItem>
                                ))}
                            </Select>
                        </FormControl>

                        <FormControl className={s.formControl}>
                            <InputLabel id="minutes-select-label">Minutes</InputLabel>
                            <Select
                                labelId="minutes-select-label"
                                id="minutes-select"
                                onClose={handleCloseInput}
                                onOpen={handleOpenInput}
                                value={inputMinutes}
                                label="Minutes"
                                onChange={selectMinutes}
                            >
                                {minutesArray.map((min) => (
                                    <MenuItem key={min} value={min}>
                                        {min}
                                    </MenuItem>
                                ))}
                            </Select>
                        </FormControl>
                    </div>
                    <div className={s.functionsContainer}>
                        <div className={s.functionsBlock}>
                            <div className={
                                selectedDays.length > 2 ? s.repeatBlockWithParameters : s.repeatBlock
                            }>

                                <span className={s.funcTitle}>Repeat</span>
                                <div className={s.rightSideContent} onClick={handleOpenDaysList}>
                                    <span
                                        className={selectedDays.length > 2 ? s.descriptionForWeekDays : s.description}>
                                        {selectedDays.length > 0
                                            ? handleDaysOutputForForm(selectedDays)
                                            : "Never"
                                        }
                                    </span>
                                    <KeyboardArrowRightIcon/>
                                </div>
                                {
                                    openDaysList && (
                                        <CheckboxList onDaysChange={handleDaysChange} toggleDaysList={handleCloseDaysList}/>
                                    )
                                }
                            </div>
                            <div className={s.line}></div>
                            <div className={s.melodyBlock}>
                                <span className={s.funcTitle}>Melody</span>
                                <div className={s.rightSideContent} onClick={handleOpenMelodyList}>
                                    <span
                                        className={s.description}>{selectedMelody
                                        ? handleMelodyOutPutForForm(selectedMelody)
                                        : "None"}</span>
                                    <KeyboardArrowRightIcon/>
                                </div>
                                {
                                    openMelodyList && (
                                        <CheckboxMelodyList onMelodyChange={handleMelodyChange}
                                                            toggleMelodyList={handleCloseMelodyList}/>
                                    )
                                }
                            </div>
                            <div className={s.line}></div>
                            <div className={s.labelBlock}>
                                <span className={s.funcTitle}>Label</span>
                                <div className={s.rightSideContent} onClick={handleLabelClick}>
                                    {isEditing? (
                                        <input
                                            type="text"
                                            value={label}
                                            onBlur={handleBlur}
                                            autoFocus
                                            className={s.inputStyle}
                                            onChange={handleChange}
                                        />
                                    ) : (
                                        <span className={s.description}>{label}</span>
                                    )}

                                </div>

                            </div>
                        </div>

                        <div className={s.buttonsBlock}>
                            <Button classes={{root: s.customTextButton}} onClick={closeDialog}>Cancel</Button>
                            <Button classes={{root: s.customTextButton}} onClick={handleAddAlarm}>Add</Button>
                        </div>
                    </div>
                </div>
                <DialogActions>

                </DialogActions>
            </Dialog>
        </React.Fragment>
    );
}
