import React, {useState, useEffect} from "react";
import s from '../stylesForClock/date.module.css'

export const ObjectDate = () => {
    const [date, setDate] = useState(
        {day: '', month: '', year: '', weekday: ''}
    );

    useEffect(() => {
        const dateObject = new Date();

        const day = dateObject.getDate().toString();
        const month = (dateObject.getMonth() + 1).toString();
        const year = dateObject.getFullYear().toString();
        const weekdays = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];
        const weekday = weekdays[dateObject.getDay()];

        setDate({day, month, year, weekday});
    }, []);

    return (
        <div className={s.dateContainer}>
            <div className={s.dateWeekday}>
                {date.weekday}
            </div>
            <div className={s.date}>
                {date.day}
                {'.'}
                {date.month}
                {'.'}
                {date.year}
            </div>
        </div>
    );
}
